import { Container, Grid, GridItem, Heading, Text} from '@chakra-ui/react'
import SimpleWordCloud from './SimpleWordCloud'
import { motion, useAnimation, AnimatePresence } from 'framer-motion'
import { useInView } from 'react-intersection-observer';
import {useEffect} from 'react'


function Skills() {
    const draw = {
        hidden: { strokeDashoffset: 1000},
        visible: {
            strokeDashoffset: 0,
            transition: {
              strokeDashoffset: { duration: 10 },
          }
        }
      };

    const Line = ({percentage}) => {
        const controls = useAnimation();
        const [ref, inView] = useInView();
        useEffect(() => {
            if (inView) {
            controls.start("visible");
            } else {
                controls.start("hidden")
            }
        }, [controls, inView]);
        return (
            <motion.svg
            width="100%"
            height="10"
            viewBox="0 0 100% 10"
            initial="hidden"
            animate={controls}
            ref={ref}
            >
            <motion.line
                z='0'
                x1="0"
                y1="5"
                x2="100%"
                y2="5"
                stroke="#FFF"
                pathLength="100"
                strokeDasharray="1000"
            />
            
            <motion.line
                z='1'
                x1="0"
                y1="5"
                x2={percentage}
                y2="5"
                stroke="#30D1FA"
                pathLength="100"
                strokeDasharray="1000"
                variants={draw}
            />
             </motion.svg>
        )
    }
      
    

    return(
        <Container  maxW='1200px' h='90vh' p='0px'>
            <Grid mt='5vh' h="90vh" mx='50px' templateRows="repeat(2, 1fr)" templateColumns="repeat(12, 1fr)" gap={1}>
            <GridItem overflow='auto' colStart={8} colSpan={5} rowStart={1} rowSpan={1} maxH='50vh'>
                <Heading> Skills </Heading>
                <Text>
                    Over the course of my studies, I have picked up various languages while working on projects involving artificial intelligence, website deployments and data management.
                </Text>
            </GridItem>
            <GridItem rowStart={1} rowSpan={1} colStart={1} colSpan={7} maxH='50vh'>
                <SimpleWordCloud />
            </GridItem>
            <GridItem rowStart={2} rowSpan={1} colStart={1} colSpan={12}>
                <Text my='5px'>Artificial Intelligence</Text>
                    <Line percentage = "75%" />
                <Text my='5px' >Website Development</Text>
                    <Line percentage = "50%" />
                <Text my='5px'>Data Management</Text>
                    <Line percentage = "40%" />
            </GridItem>
            </Grid>
        </Container>
    )
}

export default Skills