import React, { useEffect } from "react"
import { useAnimation, motion, AnimatePresence } from "framer-motion";
import { useInView } from "react-intersection-observer";
import { Box, AspectRatio, Button, Icon, Link, Text} from '@chakra-ui/react'
import { Image } from '@chakra-ui/image'
import { GoMarkGithub } from "react-icons/go";
import {CgWebsite} from 'react-icons/cg'


function Github({props}) {
    if (props.github) {
        return(
            <Link href={props.github} isExternal>
                <Button _hover={{background:'black'}}  ml='5px' px='3px' leftIcon = {<Icon as={GoMarkGithub}/>} color='white' fontSize='12px' size='xs' variant='outline'>
                        View Source
                </Button>
            </Link>
        )
    }
    else if (props.website) {
        return(
            <Link href={props.website} isExternal>
                <Button _hover={{background:'black'}} ml='5px' px='3px' leftIcon = {<Icon as={CgWebsite}/>} color='white' fontSize='12px' size='xs' variant='outline'>
                        View Website
                </Button>
            </Link>
        )
    }
}

const squareVariants = {
    hidden: { opacity: 0, scale: 0.5 },
    visible: (i) => {
        const delay = 0.5 + i*0.5;
        return (
            { opacity: 1, scale: 1, transition: {delay, duration: 0.5 } }
        )
    }
  };
  

function Square(props) {
    const controls = useAnimation();
    const [ref, inView] = useInView();
    useEffect(() => {
        if (inView) {
        controls.start("visible");
        }
    }, [controls, inView]);

    return (
        <AnimatePresence>
        <motion.div
            ref={ref}
            animate={controls}
            initial="hidden"
            variants={squareVariants}
            custom={props.index}
        > {props.children} </motion.div>
        </AnimatePresence>
    );
}

function ProjectCard({props, index}) {
    return(
        <Square index={index}>
            <motion.div whileHover={{scale:1.1}}>
                <Box align='left' p='10px' w='170px' boxShadow='2xl' maxH='300px' borderRadius='10px' overflow='hidden'>
                    <Box>              
                        <Box>
                            <AspectRatio ratio ={4/3}>
                                <Image objectFit='cover' borderRadius='10px' p='0px' src={props.image} alt='AI algorithm' />
                            </AspectRatio>
                        </Box>
                    </Box>
                    <Box isTruncated maxH='48px' color='white' fontFamily='Courier New' fontWeight='semibold' p='3px'>
                    {props.title}
                    </Box>
                    <Box overflow='auto' h='110px' fontSize='12' p='3px'>
                        <Text> {props.desc} </Text>
                    </Box>
                    <Github props={props} />
                </Box>
            </motion.div>
        </Square>
    )
}

export default ProjectCard